import React, {Component} from 'react';
import {MyCard} from './MyCard';
import {MyModal} from './MyModal';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import {AlertModal} from './AlertModal';
import { FaLongArrowAltUp,FaLongArrowAltDown } from "react-icons/fa";
import { IoMdAdd } from "react-icons/io";
import { Spinner } from 'react-bootstrap';



export class LocationList extends Component {

    constructor(props) {
        super(props);
        this.state={
            data:[],
            data2:[],        // keep record of all locations to use on filter by name
            modal: false,
            refresh: false,
            search:'',
            delete:false,
            idToDelete:'',
            nameToDelete:'',
            loading: false,
            sortAsc: true
        }
    }

    openModal(){
        this.setState({
            modal: true
        })
    }

    hideModal(){
        this.setState({
            modal: false,
            
        })
    }

    updateFormState(){
        this.setState({
            modal: false,
            refresh: true
        })  
    }

    componentDidUpdate(){
        if(this.state.delete){
            
            fetch(`https://sofiacoelho.co.uk/api/test.php?flag=deleteLoc&id=${this.state.idToDelete}`)
            .then(response => response.json())
            .then(() => {
            
                this.setState({
                    delete: false,
                    idToDelete:'',
                    nameToDelete:'',
                    refresh: true
                    
                })
            });
        }
        if(this.state.refresh){
            fetch('https://sofiacoelho.co.uk/api/test.php?flag=location')
            .then(response => response.json()) //passes http response to javascript
            .then((json) => {//awaits promise return
            this.setState({
                data: json[0],
                refresh:false,
                loading:false
          }) 
          this.sortByAsc();
        });

        }
    }

    componentDidMount(){
        this.setState({loading: true })
        fetch('https://sofiacoelho.co.uk/api/test.php?flag=location')
        .then(response => response.json()) //passes http response to javascript
        .then((json) => {//awaits promise return
          this.setState({
              data: json[0],
              data2: json[0],
              loading: false
          }) 
          this.sortByAsc();
        });
    }

    sortByAsc(){
        var orderedData = this.state.data.sort((l1, l2) =>{
            var location1 = l1[1].toUpperCase();
            var location2 = l2[1].toUpperCase();
            if(location1 < location2){
                return -1;
            }else if(location1 > location2){
                return 1;
            }else{
                return 0;
            }
        })
        this.setState({
            data:orderedData
        })
    }
    sortByDesc(){
        var orderedData = this.state.data.sort((l1, l2) =>{
            var location1 = l1[1].toUpperCase();
            var location2 = l2[1].toUpperCase();
            if(location1 < location2){
                return 1;
            }else if(location1 > location2){
                return -1;
            }else{
                return 0;
            }
        })
        this.setState({
            data:orderedData
        })

    }
    
    searchByName(e){
        
        this.setState({
            search : e.target.value,
            data: this.state.data2.filter(data => data[1].toUpperCase().includes(e.target.value.toUpperCase()) )
        })
    }

    delete(param1,param2){
        this.setState({
            showAlert:true,
            idToDelete: param1,
            nameToDelete: param2
            
        })
    }

    runDelete(){
        this.setState({
            delete:true,
            showAlert:false
        })
    }
    hideAlert(){
        this.setState({
            showAlert:false
        })
    }
    sortToggle(){
        if(this.state.sortAsc){
            this.sortByDesc();
            this.setState({sortAsc: false})
        }else{
            this.sortByAsc();
            this.setState({sortAsc: true})
        }
    }

    capitalizeString(string) {
        return string.charAt(0).toUpperCase() + string.slice(1);
    }

      render() {
        
          var table = true;
          if(this.state.loading){

            table = false;
          }
        return (
            <>
        
            <div className= 'row mt-4 '>
                <div className= 'col-7 col-sm-8 col-md-7 mb-3'>
                    <Form.Control type="text" placeholder="Search by name" value ={this.state.search} onChange={this.searchByName.bind(this)} />
                </div>
                
                <div className= 'col-5 col-sm-4 col-md-5 mb-3'>
                    <Button variant='link' onClick = {this.sortToggle.bind(this)}>Name {this.state.sortAsc? <FaLongArrowAltUp></FaLongArrowAltUp>:<FaLongArrowAltDown></FaLongArrowAltDown>}</Button>
                    <Button variant='link' onClick = {this.openModal.bind(this)}><IoMdAdd></IoMdAdd></Button>
                </div>
            </div>
         

            <AlertModal hide = {this.hideAlert.bind(this)} show={this.state.showAlert} delete={this.runDelete.bind(this)} name={this.state.nameToDelete}></AlertModal>
            <MyModal formOnSubmit = {this.updateFormState.bind(this)} show = {this.state.modal} hide = {this.hideModal.bind(this)} flag = 'addLocation'/>
            {table?
                <div className='row justify-content-between'>
                    {this.state.data == ''?<p>No Locations</p>:
                    this.state.data.map((location) =>
                    location[0]== 0?                    //if location has no employees add option that allows deletion
                    <MyCard  delete={this.delete.bind(this, location[2], location[1])} flag='location' delFlag = 'delete' useMatch = {this.props.match} id = {location[2]} key = {location[2]} name = {this.capitalizeString(location[1])} emps = {location[0]} />
                    :
                    <MyCard  flag='location' useMatch = {this.props.match} id = {location[2]} key = {location[2]} name = {this.capitalizeString(location[1])} emps = {location[0]} />
                    )
                    }
                    
                </div>: <div className="text-center"><Spinner animation="border" ></Spinner></div>}
            </>
        );
    }
    
}